import { getGenArtToken, getGenMintToken } from "../buildr/tokens";
import {
    useABI_ERC21_GenArtContract,
    useABI_ERC21_EyesFiClubContract,
    useABI_ERC21_EyesFiMintContract
} from '@/contacthelp/useContract.js';

import config from '@/config/config.js';


export function getArtcontract(library, account, chainID) {
    console.log('getArtcontract');
    const token = getGenArtToken(chainID);
    const GenArtContract = useABI_ERC21_EyesFiClubContract(library,
        account,
        token.address,
        true);
    return GenArtContract;

}

export function getEyesFiMintcontract(library, account, chainID) {
    console.log('getArtcontract');
    const token = getGenMintToken(chainID);
    const GenArtContract = useABI_ERC21_EyesFiMintContract(library,
        account,
        token.address,
        true);
    return GenArtContract;

}

export async function addProject({ library, account, chainID, projectName, artistAddress, pricePerTokenInWei, iscode }) {
    const Artcontract = getArtcontract(library, account, chainID);
    const ProjectID = await Artcontract.nextProjectId();
    const tx = await Artcontract.addProject(projectName, artistAddress, pricePerTokenInWei, iscode);
    return {
        ProjectID,
        tx
    };

}

