export async function purchase({ library, account, chainID, projectId, pricePerTokenInWei, isPassport, passportId }) {
    console.log({ library, account, chainID, projectId, pricePerTokenInWei, passportId });
    const Artcontract = getEyesFiMintcontract(library, account, chainID);

    console.log({ Artcontract });
    const info = await Artcontract.purchase(projectId, isPassport, passportId, {
        value: pricePerTokenInWei
    });
    return info;

}

export async function purchaseTo({ to, library, account, chainID, projectId, pricePerTokenInWei, isPassport, passportId }) {
    console.log({ to, library, account, chainID, projectId, pricePerTokenInWei, isPassport, passportId });
    const Artcontract = getEyesFiMintcontract(library, account, chainID);

    console.log({ Artcontract });
    const info = await Artcontract.purchaseTo(to, projectId, isPassport, passportId, {
        value: pricePerTokenInWei
    });
    return info;

}

